import React from 'react';
import PropTypes from 'prop-types';
import { Form } from 'redux-form';
import { Link } from 'react-router-dom';
import path from '../../helpers/path';
import APInput from '../common/APInput/APInput';
import APIcon from '../common/APIcon/APIcon';
import APButton from '../common/APButton';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import APRadio from '../common/APRadio/APRadio';
import { required, email } from '../../helpers/formValidations';
import config from '../../config';
import { styleNames } from '../../propTypes/commonComponent';

import '../../styles/components/common/StandaloneForm.css';
import '../ForgotPassword/ForgotPasswordFormComponent.css';

const baseClasses = getBEMClasses('standalone-form');
const forgotPasswordFormClasses = getBEMClasses('forgot-password-form');

export class ForgotPasswordFormComponent extends React.Component {
  constructor(props) {
    super(props);

    this.state = { resetPassword: true };
  }

  toggleFormType = () =>
    this.setState({ resetPassword: !this.state.resetPassword });

  sendEmail = values => {
    const { sendResetPasswordEmail, resendVerificationEmail } = this.props;

    if (this.state.resetPassword) {
      return sendResetPasswordEmail(values.Email);
    }

    return resendVerificationEmail(values);
  };

  render() {
    const { context } = this;
    const { handleSubmit, submitting, pristine, invalid } = this.props;
    const { useEmailAsUsername } = config.SignupForm;
    const { showOptions } = config.ProblemLoggingIn;

    let validate = [required];
    let label = context.t('Username');

    if (useEmailAsUsername) {
      validate.push(email);
      label = context.t('Email');
    }

    return (
      <div
        className={`${baseClasses('container')} ${forgotPasswordFormClasses(
          'container'
        )}`}>
        <div
          className={`${baseClasses('header')} ${forgotPasswordFormClasses(
            'header'
          )}`}>
          <div
            className={`${baseClasses(
              'header-text'
            )} ${forgotPasswordFormClasses('header-text')}`}>
            {context.t('Problems Logging In?')}
          </div>
          <Link to={path('/')} className={baseClasses('close-icon')}>
            <APIcon name="close" classModifiers="big" />
          </Link>
        </div>
        <Form
          onSubmit={handleSubmit(this.sendEmail)}
          className={`${baseClasses('form')} ${forgotPasswordFormClasses(
            'form'
          )}`}>
          {showOptions ? (
            <React.Fragment>
              <div
                className={`${baseClasses(
                  'information-text'
                )} ${forgotPasswordFormClasses('information-text')}`}>
                {context.t(
                  'Select your issue type and provide us with your {label} below. We will follow up with a solution shortly.',
                  { label: label.toLowerCase() }
                )}
              </div>
              <hr className={baseClasses('separator')} />
              <section className={baseClasses('radio-list')}>
                <APRadio
                  name="type"
                  checked={this.state.resetPassword}
                  onChange={this.toggleFormType}
                  customClass={forgotPasswordFormClasses()}
                  label={context.t(
                    'I’m an existing user and I forgot my password.'
                  )}
                  id="problem-login-radio1"
                />
                <APRadio
                  name="type"
                  checked={!this.state.resetPassword}
                  onChange={this.toggleFormType}
                  customClass={forgotPasswordFormClasses()}
                  label={context.t(
                    'I’m a new user and I haven’t received my verification email.'
                  )}
                  id="problem-login-radio2"
                />
              </section>
            </React.Fragment>
          ) : (
            <div
              className={`${baseClasses(
                'information-text'
              )} ${forgotPasswordFormClasses('information-text')}`}>
              {context.t(
                'Provide us with your {label} below. We will follow up with reset instructions.',
                { label: label.toLowerCase() }
              )}
            </div>
          )}
          <APInput
            type="text"
            name="Email"
            customClass={forgotPasswordFormClasses()}
            label={label}
            validate={validate}
          />
          <hr className={forgotPasswordFormClasses('separator')} />
          <APButton
            type="submit"
            disabled={submitting || pristine || invalid}
            customClass={forgotPasswordFormClasses()}
            styleName={styleNames.additive}>
            {submitting ? context.t('Processing...') : context.t('Send Email')}
          </APButton>
        </Form>

        <div
          className={`${baseClasses('footer')} ${forgotPasswordFormClasses(
            'footer'
          )}`}>
          <Link
            to={path('/login')}
            className={forgotPasswordFormClasses('link')}>
            {context.t('Log In')}
          </Link>
          <div className={baseClasses('footer-separator')} />
          <Link
            to={path('/signup')}
            className={forgotPasswordFormClasses('link')}>
            {context.t('Sign Up')}
          </Link>
        </div>
      </div>
    );
  }
}
ForgotPasswordFormComponent.propTypes = {
  handleSubmit: PropTypes.func.isRequired
};

ForgotPasswordFormComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default ForgotPasswordFormComponent;



