import React from 'react';
import PropTypes from 'prop-types';
import { Field } from 'redux-form';

import { getBEMClasses } from '../../../helpers/cssClassesHelper';

const baseClass = 'ap-radio';

export const InnerRadioInput = props => {
  const {
    input,
    customClass,
    meta,
    inline,
    label,
    info,
    id,
    ...passedProps
  } = props;

  const bemClasses = getBEMClasses([baseClass, customClass]);

  delete passedProps.meta;

  return (
    <div className={bemClasses(null, { inline })}>
      <input
        className={bemClasses('input')}
        type="radio"
        {...input}
        {...passedProps}
        id={id}
      />
      <label className={bemClasses('label')} htmlFor={id}>
        {label}
        <span className={bemClasses('check')} />
        <span className={bemClasses('checked')} />
      </label>

      {info && (
        <small className={`form-text-muted ${bemClasses('radio-info')}`}>
          {info}
        </small>
      )}

      {meta.dirty &&
        meta.error && (
          <span className={bemClasses('radio-error')}>{meta.error}</span>
        )}
    </div>
  );
};

const APRadio = props => {
  return (
    <Field
      name={props.name}
      component={InnerRadioInput}
      type="radio"
      {...props}
    />
  );
};

APRadio.defaultProps = {
  name: '',
  label: '',
  autoFocus: false,
  required: false,
  readOnly: false,
  inline: false,
  info: ''
};

APRadio.propTypes = {
  name: PropTypes.string,
  label: PropTypes.string,
  customClass: PropTypes.string,
  autoFocus: PropTypes.bool,
  required: PropTypes.bool,
  readOnly: PropTypes.bool,
  inline: PropTypes.bool,
  info: PropTypes.string,
  id: PropTypes.string.isRequired
};

export default APRadio;



