import { connect } from 'react-redux';
import { reduxForm } from 'redux-form';
import ProblemLoggingInFormComponent from './ProblemLoggingInFormComponent';
import {
  sendResetPasswordEmail,
  resendVerificationEmail
} from '../../redux/actions/userActions';

const ProblemLoggingInForm = reduxForm({
  form: 'forgotPassword',
  onSubmit: (payload, dispatch) => {
    const { resetPassword, Email } = payload;
    if (resetPassword) return dispatch(sendResetPasswordEmail(Email));

    return dispatch(resendVerificationEmail({ Email }));
  },
  enableReinitialize: true
})(ProblemLoggingInFormComponent);

const mapDispatchToProps = dispatch => ({
  sendResetPasswordEmail,
  resendVerificationEmail
});

const ProblemLoggingInFormContainer = connect(
  null,
  mapDispatchToProps()
)(ProblemLoggingInForm);

export default ProblemLoggingInFormContainer;



